color-legend.styled {
    text-align: center;
    --cle-font-size-title: 9pt;
    --cle-background: unset;/* #333333b3; */
    --cle-letter-spacing-title: 0.5px;
    --cle-color: white;
    --cle-padding: 0.25rem 0.25rem 0.75rem;
}

#map-legend-panel {
    display: inline;
    background: #333333b3;
}

#info-legend {
    display: inline;
}

#info-legend > button {
    font-size: 9px;
    border-radius: 12px;
    width: 20px;
    height: 20px;
    border: 1px solid rgba(0,0,0,0.2);
    margin-top: 4px;
}